<script>
import NavBar from "components/common/navbar/NavBar";
import { mapState, mapGetters, mapMutations } from "vuex";

export default {
  name: "order",
  data() {
    return {
      addressList: [],
      address: {},
      deliveryFee: 0,
      backNote: "",
      show: false
    };
  },
  computed: {
    ...mapState(["chooseAddressId"]),
    ...mapGetters(["order", "orderPrice"]),
    total() {
      return this.orderPrice + this.deliveryFee;
    }
  },
  components: {
    NavBar
  },
  created() {
    this.fetchAddressList();
  },
  mounted() {
    this.fetchAddressList();

    // this.$nextTick(()=>{
    //   this.fetchDeliveFee();
    // })

    // console.log(this.order);
  },
  methods: {
    ...mapMutations(["removeCheckGoodsFromCart"]),
    handlePay() {
      this.show = true;
    },
    async handleEnSure() {
      let res = await this.$http.get(this.$api.WXPAY);
      if (res.data.code === 200) {
        // 提交订单信息 用户 商品 配送信息
        let obj = {
          uid: localStorage.uid,
          goods: this.order,
          aid: this.address.id,
          total: this.total,
          backNote: this.backNote,
          state: 1,
          
        };
        setTimeout(() => {}, 1000);
        // console.log(obj);
        // 提交订单
        let res = await this.$http.post(this.$api.ORDER, obj);
        console.log(res);
        if (res.data.code == 200) {
          let total = this.total;
          // 删除购物车当中已提交的订单
          this.removeCheckGoodsFromCart();
          // 跳转到提示页面
          this.$router.push({ name: "success", query: { total } });
        }
      }
    },
    // 获取邮费信息
    async fetchDeliveFee() {
      // 当前订单商品信息
      let goods = this.order;
      // 获取每个商品的配送模板的详情信息
      // let total = 0
      console.log(this.order);

      goods.forEach(async v => {
        let res = await this.$http.get(this.$api.DDETAIL, { did: v.tid });
        console.log(res);
        let obj = res.data.data;
        // 根据当前的收获地址获取配送价格
        let obj1 = obj.find(v => v.areas === this.address.area);
        console.log(obj.price);
        //  total += obj;
        this.deliveryFee += obj1.price;
        // this.deliveryFee += parseFloat(obj.price);
      });
      // this.deliveryFee = total;
      //  console.log(this.deliveryFee)
    },
    async fetchAddressList() {
      let res = await this.$http.get(this.$api.ADDRESS, {
        uid: 1
      });
      // console.log(res);
      this.addressList = res.data.data;
      // 没有选择过地址
      if (this.addressList.length === 0) {
        return;
      }
      if (this.chooseAddressId === "") {
        // 从当前用户的地址列表中找设置了默认地址的数据
        let obj = this.addressList.find(v => v.default === 1);
        // 如果有就是默认地址 如果没有地址就是列表的第0个
        this.address = obj || this.addressList[0];
      } else {
        // 说明选择过地址
        let obj = this.addressList.find(v => v.id === this.chooseAddressId);
        this.address = obj;
      }
      this.fetchDeliveFee();
    }
  }
};
</script>
